.header__link {
  transition: 0.5s;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  color: #fff;
  line-height: 22px;
  margin: 0;
  padding: 0;
}

.header__link:hover {
  opacity: 0.6;
}

@media screen and (max-width: 979px) {
  .header__link {
    font-size: 13px;
  }
}